import { Component } from "@angular/core";
import { GlobalComponent } from "../../components/global/global";
import { AuthServicesProvider } from "../../providers/auth-services/auth-services";
import { LineMessageServicesProvider } from "../../providers/line-message-services/line-message-services";
import {IonicPage, AlertController, Platform, ViewController } from "ionic-angular";
import {
  NavController,
  NavParams,
  LoadingController,
  ModalController
} from "ionic-angular";
import { MyAppSharedSettings } from "../../Model/MyAppSharedSettings";

/**
 * Generated class for the LineMessagePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-line-message-log',
  templateUrl: 'line-message-log.html',
})
export class LineMessageLogPage {
  CanSch: boolean;
  CanEditConn: boolean;
  selected = [];
  selectedItem:any;
  offset:number=0;
  limit:number=8;
  loadPages:number=0;
  loadPageNumber:number=1;
  data_list = [];
  ConnExt_page: string;
  detail_page: string;
  sql_page: string;
  err_msg: string;
  is_err: boolean;
  innerWidth: number;
  innerHeight: number;

  is_loading: boolean;
  pageNumber: number = 1;
  pageSize: number = 24;
  totalPages: number = 1;
  totalRows: number = 0;
  can_del: boolean;
  current_item: any;
  mode: string;
  bordered_class: string;
  Conn_page: string;
  AddEditPage: string;
  keyword: string = "";
  title:string;
  subject_no:string="";
  order_type: boolean;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private global: GlobalComponent,
    public authServices: AuthServicesProvider,
    public LineMessageServices: LineMessageServicesProvider,
    public modalCtrl: ModalController,
    public platform:Platform,
    public conf: MyAppSharedSettings,
    public viewCtrl: ViewController,
  ) {

    this.subject_no = navParams.data.item.subject_no;
    this.title = navParams.data.item.subject_no;
    this.innerWidth = window.innerWidth;
    this.innerHeight = window.innerHeight;
    this.order_type = false;
    if (this.authServices.authenticated() === true) {
      this.CanSch = this.authServices.CanSch();
      this.LoadData(true);
    }
  }
  ngOnInit() {

    this.pageNumber = 1;
    this.offset=0;
    this.limit=Math.floor(this.pageSize/3);

  }
  onRowSelect({ selected }) {
    this.selectedItem=selected[0];

  }
  setPage(pageInfo) {
    this.pageNumber = Math.floor(pageInfo.offset / 3) + 1;
    if ((pageInfo.offset + 1) % 3 === 0) {
      if ((pageInfo.offset + 1) / 3 + 1 > this.loadPages) {
        this.loadPageNumber=this.pageNumber+1;
        this.LoadData(false);
      }
    }
  }
  onResize(event) {
    this.innerWidth = event.target.innerWidth;
    this.innerHeight = event.target.innerHeight;
  }
  doInfinite(): Promise<any> {
    console.log("Begin async operation");
    if(this.loadPages<this.totalPages)
    {
      this.pageNumber++;
      this.loadPageNumber++;
      return new Promise(resolve => {
        setTimeout(() => {
          this.LoadData(false);
          console.log("Async operation has ended");
          resolve();
        }, 500);
      });
    }
  }
  LoadData(init: boolean) {
    if (init) {
      this.data_list = [];
      this.pageNumber = 1;
      this.loadPageNumber=1;
      this.offset = 0;
      this.loadPages = 0;
      this.limit === Math.floor(this.pageSize / 3);
    }
    this.global.createLoader();
    this.global.loading.present().then(() => {

        this.LineMessageServices.GetLogListsByAsync(
          this.pageSize,
          this.loadPageNumber,
          this.keyword,
          this.subject_no,
          this.order_type,
        ).subscribe(
          data => {
            if (data.DidError === true) {
              this.global.dismissLoading();
              this.global.showError(data.ErrorMessage);
            } else {
              if (init) {
                this.data_list = data.Model;
                this.totalRows = data.TotalRows;
              } else
                for (let i = 0; i < data.Model.length; i++) {
                  this.data_list.push(data.Model[i]);
                }
                if (this.totalRows > 0) {
                  this.selectedItem = this.data_list[0];
                  this.selected = [this.selectedItem];
                  this.data_list = [...this.data_list];
                  this.loadPages++;
                } else {
                  this.selectedItem = null;
                  this.selected = null;
                }
              this.totalPages = data.TotalPages;
              this.global.dismissLoading();
            }
          },
          err => {
            this.global.dismissLoading();
            this.global.showError("WebAPI-"+err.message);
          }
        );

    });
  }
  get_board_class(item) {
     return "bordered";
  }


  ionViewDidLoad() {
    console.log('ionViewDidLoad LineMessageLogPage');
  }

}



