import { AuthServicesProvider } from "./../../providers/auth-services/auth-services";
import { Component, Input } from "@angular/core";
import { MenuController, NavController } from "ionic-angular";
import { MenuViewModel } from "../../Model/ViewModel/MenuViewModel";
import { UserMenuResponse } from "../../Model/Response/UserMenuResponse";
import { String } from "../../Model/String";
import { MyAppSharedSettings } from "../../Model/MyAppSharedSettings";
import { MyApp } from "../../app/app.component";
import { ProgramPageMenuViewModel } from "../../Model/ViewModel/ProgramPageMenuViewModel";
import { GlobalComponent } from "../global/global";

@Component({
  selector: "menuComponent",
  templateUrl: "menu.html"
})
export class MenuComponent {
  @Input() menu_no: string;
  public menu_list: MenuViewModel[];
  User: UserMenuResponse;
  menu_id: string;
  init: boolean = true;
  constructor(
    public menuCtrl: MenuController,
    public navCtrl: NavController,
    private auth: AuthServicesProvider,
    private conf: MyAppSharedSettings,
    private myApp: MyApp,
    private global: GlobalComponent
  ) {}
  LoadData(menu_no: string) {
    this.menu_no = menu_no;
    this.menu_id = String.Format("{0}_{1}", menu_no, this.conf.login_count);

    this.User = this.auth.getUserInfo();
    if (this.User != null) {
      this.menu_list = this.User.Model.MenuViewModel;
      this.myApp.menu_id = this.menu_id;
      this.myApp.userName = this.conf.userName;
      this.myApp.api_name = this.conf.api_name;
    }

    if (this.menu_list != null) {
      this.menu_list = this.menu_list.filter(item => {
        return item.menu_no === this.menu_no;
      });
    }
  }
  ngOnInit() {
    this.LoadData(this.menu_no);
  }

  openPage(page: ProgramPageMenuViewModel) {
    this.global.createLoader();
    this.global.loading.present().then(() => {
      if (
        page.page_name.indexOf("Management") > -1 ||
        page.page_name.indexOf("Home") > -1
      )
      {
        this.navCtrl.setRoot(page.page_name);
      }
      else this.navCtrl.push(page.page_name);
     });

  }
}



