import { Directive, HostListener, ElementRef, OnInit } from '@angular/core';
import { GlobalComponent } from '../../components/global/global';

/**
 * Generated class for the AutosizeDirective directive.
 *
 * See https://angular.io/api/core/Directive for more info on Angular
 * Directives.
 */
@Directive({
  selector: 'ion-textarea[autosize]' // Attribute selector
})
export class AutosizeDirective implements OnInit {
  @HostListener("input", ["$event.target"])
	onInput(textArea: HTMLTextAreaElement): void {
		this.global.adjust(this.element);
	}
  constructor(public element:ElementRef,public global: GlobalComponent,) {
   console.log('Hello AutosizeDirective Directive');
  }
  ngOnInit(): void {
		setTimeout(() => 	this.global.adjust(this.element), 0);
	}
}



